	top: -999999px;
	z-index: 1;
	width: 130px;
	height: 40px;
	padding-left: 15px;
	box-shadow: 3px 3px 5px rgb(220, 220, 220);
}

.menu ul {
	list-style: none
}

.menu ul li {
	padding: 5px 10px;
	color: #228EFB;
	border-bottom: 1px dashed #228EFB;
	font-size: 17px;
}

.menu ul li:last-child {
	border-bottom: none;
}
</style>



